$(function () {

	if (platepoints_awarded)
	{
		opener.dd_toast({message: 'You earned ' + platepoints_awarded + ' PLATEPOINTS!', position: 'topcenter', css_style: 'platepoints'});
	}

	if (reload_dreamdinners)
	{
		opener.location.reload(true);
	}

	if (js_bounce)
	{
		if(window.opener && !window.opener.closed)
		{
			opener.bounce(js_bounce);
		}
	}

	if(window.opener && !window.opener.closed)
	{
		self.close();
	}

});